@font-face {
  font-family: "socialshare";
  src: url("fonts/socialshare.ttf?smrq5k") format("truetype"), url("fonts/socialshare.woff?smrq5k") format("woff"), url("fonts/socialshare.svg?smrq5k#socialshare") format("svg");
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "socialshare" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  position: relative;
  text-decoration: none;
  margin: 4px;
  display: inline-block;
  outline: none;
}
.social-share-icon {
  position: relative;
  display: inline-block;
  width: 32px;
  height: 32px;
  font-size: 20px;
  border-radius: 50%;
  line-height: 32px;
  border: 1px solid #666;
  color: #666;
  text-align: center;
  vertical-align: middle;
  transition: background 0.6s ease-out 0s;
}
.social-share-icon:hover {
  background: #666;
  color: #fff;
}
.icon-digg {
  color: #2a2a2a;
  border-color: #2a2a2a;
}
.icon-digg:before {
